.post-detail {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 20px;
  padding-bottom: 320px;
}

.post-content {
  background: #fff;
  padding: 48px 60px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
  min-height: 1300px;
  margin-bottom: 64px;
}

.post-content h1 {
  font-size: 32px;
  font-weight: 600;
  color: #222;
  margin-bottom: 32px;
}

.post-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 32px;
}

.post-meta .author {
  font-weight: 500;
  color: #333;
}

.post-meta .date {
  color: #666;
}

.post-meta .actions {
  color: #666;
}

.post-tags .ant-tag {
  padding: 4px 12px;
  font-size: 14px;
  border-radius: 16px;
  margin-right: 12px;
}

.post-text {
  font-size: 16px;
  line-height: 1.8;
  color: #333;
  padding: 0 20px;
}

.post-text p {
  margin-bottom: 24px;
  line-height: 1.8;
}

.post-text p:last-child {
  margin-bottom: 0;
}

.post-comments {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fff;
  box-shadow: 0 -2px 8px rgba(0, 0, 0, 0.1);
  z-index: 100;
  height: 64px;
  display: flex;
  flex-direction: column;
  transition: height 0.3s ease;
}

.post-comments:hover {
  height: 600px;
}

.post-comments-inner {
  max-width: 1000px;
  margin: 0 auto;
  padding: 16px 20px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.post-comments .ant-card {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s ease;
  margin-top: -100%;
}

.post-comments:hover .ant-card {
  opacity: 1;
  visibility: visible;
  margin-top: 0;
}

.post-comments-header {
  position: sticky;
  top: 0;
  background: #fff;
  z-index: 1;
  padding-bottom: 16px;
}

.post-comments-title {
  margin: 0 !important;
}

.post-comments .ant-card {
  background: transparent;
  box-shadow: none;
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.post-comments .ant-card-body {
  padding: 0;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.comment-list-container {
  flex: 1;
  overflow-y: auto;
  padding-right: 16px;
}

.comment-list {
  max-height: 400px;
  overflow-y: auto;
  padding-right: 16px;
}

.comment-list::-webkit-scrollbar {
  width: 6px;
}

.comment-list::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

.comment-list::-webkit-scrollbar-thumb {
  background: #ccc;
  border-radius: 3px;
}

.comment-list::-webkit-scrollbar-thumb:hover {
  background: #999;
}

.comment-item {
  display: flex;
  align-items: flex-start;
  width: 100%;
}

.comment-content {
  margin-left: 16px;
  flex: 1;
}

.comment-header {
  margin-bottom: 8px;
}

.comment-author {
  font-weight: 500;
  color: #333;
  margin-right: 12px;
}

.comment-date {
  color: #999;
  font-size: 14px;
}

.comment-text {
  color: #333;
  line-height: 1.6;
}

.comment-textarea {
  position: sticky;
  top: 0;
  background: #fff;
  z-index: 1;
  padding-bottom: 16px;
  transition: all 0.3s ease;
}

.comment-textarea .ant-input {
  resize: none;
  height: 36px !important;
  transition: all 0.3s ease;
}

.comment-textarea .ant-input:focus {
  height: 100px !important;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .post-content {
    padding: 32px 24px;
    min-height: 400px;
    margin-bottom: 32px;
  }

  .post-content h1 {
    font-size: 24px;
  }

  .post-text {
    font-size: 15px;
  }

  .post-detail {
    padding-bottom: 340px;
  }

  .post-comments:hover {
    height: 500px;
  }

  .post-comments-inner {
    padding: 12px 16px;
  }

  .comment-list {
    max-height: 240px;
  }
}
